
//FROM 768PX --------------------------------------------------------------------------------
@media (min-width: $break-sm) {

    nav {
        flex-wrap: nowrap;
        width: 750px;
        height: 110px;
        margin: 0 auto;
        padding-bottom: 0;
    }

    #logo {
        width: 375px;
        padding-right: 15px;
    }

    #logo img{
        width: 345px;
    }

    //NAV SEARCH BAR moves up- not accurate, testing. also settings for moving button to side
    #search-bar {
        display: inline-block;
        display: flex;
        flex-direction: row;
        width: 240px;
        align-self: center;
        justify-self: center;
        height: 56px;
        margin-left: auto;
        //ADDITIONS FROM MEDIA QUERY CHANGES
        position: relative;
        bottom: 0;
        padding: 0 0; //not working?
        margin-top: 0; //to increase?
        margin: 0 auto;
    }

    //NAV SEARCH BAR //remove box shadow & change border
    #search-bar input[type=text] {
        outline: none;
        border: 1px solid $second-grey;
        background-color: $white;
        border-radius: 0;
        height: 56px;
        width: 174px;
        font-size: 14px;
        line-height: 20px;
        position: relative;
        right: 0;
    }

    //NAV SEARCH BAR focus blue
    #search-bar input[type=text]:focus {
        outline: 0;
        border: .5px solid #5bc0de;
        box-shadow: rgba(91, 192, 222, .6) 0px 0px 8px 0px;
    }

    #search-bar button[type=button] {
        background-color: $search-button-color1;
        height: 56px;   
        border-radius: 0px 3px 3px 0px;
        width: 63px;
        position: relative;
        left: 0;
    }


    #search-bar button[type=button] i {
        color: $white;
        height: 56px;
        line-height: 56px;
        font-size: 23px;
        width: 100%;
        text-align: center;
        padding-right: 0;
    }

    #search-bar button[type=button]:hover {
        background-color: $search-button-color2;
    }

    //NAV BAR order
    #menu {
        order: 2;
        margin-left: 20px;
    }


    //BANNER HEIGHT
    .banner {
        height: 589px;
    }

    //FOOTERS main footer splits into 2 columns.
    
    .main-footer {
        flex-direction: row; 
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: space-around;
        //padding-left: 2%;
        //padding-right: 2%;
        height: 528px;
    }

    .about,
    .contact,
    .website,
    .social {
        width: 375px;
        align-self: flex-start;
    }

    
    // .about {
    //     padding-top: 43px;
    // }

    //FOOTER width
    .main-foot-cont {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
        width: 750px;
        margin: 0 auto;
    }
   
    //DISPLAY LOGO FOOTERS
    .client-footer,
    .partner-footer {
        display: flex;
}

    .footer-logos,
    .partner-footer {
        width: 720px;
        margin: 0 auto;
    }

    //SOCIAL BUTTON WIDTH
    .social-button li a {
        width: 157.5px;
    }

    //LATEST standardising title height so doesn't matter if title is 1 or 2 lines
    .news-text > h3 {
        height: 48px;
    }

    //LATEST standardising p height so doesn't matter if text is 2 r 3 lines
    .news-text > p {
        height: 66px;
    }

    //LATEST 3rd news item hidden. //DONE
    .news-3 {
        display: none;
    }

    //NAV call button disappears. //DONE
    #direct-call {
        display: none;
    }

    //BANNER / INTRO /LATEST TITLE width
    .banner-container,
    .intro-container,
    #latest-title {
        width: 720px;
        margin: 0 auto;
        padding-left: 0;
    }

    //CARD bottom 4 cards switch to 2 column layout. TO LOOK AT MARGINS/PADDING.
    .cards {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;   ////////////check justify settings
        width: 720px; ///////////maintain equal margins
        margin-left: auto;
        margin-right: auto;
    }

    .card-software,
    .card-it,
    .card-marketing {
        width: 100%; 
        margin-left: 0;
        margin-right: 0;
    }

    .card-telecoms,
    .card-web,
    .card-cyber,
    .card-training {
        width: 343px;
    }

    .card-telecoms p,
    .card-web p,
    .card-cyber p,
    .card-training p {
        height: 66px;
    }


    .card-telecoms,
    .card-cyber {
        margin-left: 0;
    }

    .card-web,
    .card-training {
        margin-right: 0;
    }


    // .card > p {
    //     padding-top: 0;
    //     padding-bottom: auto;
    //     margin-top: 0;
    //     margin-bottom: auto;
    // }

    // .card > button {
    //     // padding-bottom: 0;
    //     // padding-top: auto;
    //     // margin-top: auto;
    //     // margin-bottom: 0;
    // }

    //LATEST switch to 2 column layout. Fixed width. 

    .news-container {
        display: flex;
        flex-direction: row;
        width: 750px;
        margin-left: 0px;
        margin-right: 0px;
    }
    
    #latest-title {
        margin: 0 auto;
    }
        
    
    .latest h2 {
        display: block;
        width: 100%;
    }
    .news {
        width: 345px;
        
    }

    .news-1 {
        margin-left: auto;
        margin-right: 15px;
    }

    .news-2 {    
        margin-left: 15px;
        margin-right: auto;
    }


    .posted-by {
        width: 308px;
    }


    .posted-by-text {
        width: 235px;
    }


    //NEWS TOOLTIPS ----------------------------------
    .news .tooltip .tooltip-text {
        width: 176px;
        right: 50%;
        margin-right: -88px;
    }

    .news .tooltip .tooltip-text > p {
        max-width: 113px; //////////////keeps all text visible
    }
    
    //bottom arrow
    .news .tooltip .tooltip-text::after {
        left: 50%;
        margin-left: -5px;
    }
}


//FROM 993PX --------------------------------------------------------------------------------------


@media (min-width: $break-med) {

        nav {
            width: 970px;
        }

        //increase logo size
        #logo img {
            width: 370px;
        }

        //NAV bar appears. //DONE
        .nav-2 {
            display: flex;
        }

        //hide search input
        #search {
            display: none;
        }

        //change search bar container width to the size of the button
        #search-bar {
            width: 63px;
            margin-left: 20px;
        }

        //search button rounded corners
        #search-bar button[type=button] {
            border-radius: $round-corner;
        }

        //NAV support and contact buttons appear
        #support,
        #contact {
            display: inline-block;
        }

        #support {
            margin-left: auto; 
        }

        //INTRO background image added. //DONE
        .intro {
            background: $main-grey url("../../html-css-reflection/images/internal-branding/logo-zoomed-transparent.png") no-repeat right bottom;
            background-size: cover;
        }

        //BANNER text aligns left & image gradient changes
        //BANNER margin-left starts expanding
        .banner {
            align-items: flex-start;
            text-align: left;
            background:  linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0.4042659300048145) 35%, rgba(0,0,0,0) 100%),
                        url("../../html-css-reflection/images/slides/slide-1.jpeg");
            background-size: cover;
            height: 490px;
        }

        //BANNER /INTRO /LATEST TITLE WIDTH
        .banner-container,
        .intro-container,
        #latest-title {
            width: 940px;
        }

        .banner-container > h1,
        .banner-container > span {
            margin-left: 0;
        } 

        //CARD formatting
        // .card {
        //     height: 297px;
        // }

        .cards {
            width: 940px; ///////////maintain equal margins
        }


        //CARD format 3 + 2 + 2. TO LOOK AT MARGINS/PADDING.
        .card-software,
        .card-it,
        .card-marketing {
            width: 293.333px;
            height: 356px;
        }

        .card-software > p,
        .card-it > p,
        .card-marketing > p {
            height: 110px;       
        }


        .card-software {
            margin-left: 0;
        }

        .card-marketing {
            margin-right: 0;
        }

        .card-telecoms,
        .card-web,
        .card-cyber,
        .card-training {
            width: 455px;
        }

        .card-telecoms p,
        .card-web p,
        .card-cyber p,
        .card-training p {
            height: 44px;
        }

        //LATEST news items new width. TO EDIT MARGINS/SPACING
        .news-container {
            width: 970px;
        }
        
        .news {
            width: 455px;
        }

        //FOOTER width
        .footer-logos,
        .partner-footer {
            width: 940px;
            margin: 0 auto;
        }

        .main-foot-cont {
            width: 970px;
            margin: 0 auto;
        }

        //FOOTER HEIGHT
        .partner-footer {
            height: 210px;
        }

        //main footer container widths
        .contact,
        .about,
        .website,
        .social {
            width: 485px;
        }

        //FOOTER CLIENT AND PARTNER LOGO RESIZING. Not sure about the larger partner logos at this size - visually prefer the settings for the next screen up due to image quality and spacing.
        
        #busseys {
            width: 168.7px;
            height: 62.8px;
        } 

        #crane {
            width: 260px; 
            height: 62.8px;
        }

        #beat { 
            width: 64.6px; 
            height: 62.8px;
        }

        #northern-diver {
            width: 289px; 
            height: 62.8px;
        }

        #microsoft {
            width: 140.2px;
            height: 85.3px;
        } 

        #future-50 {
            width: 69.2px; 
            height: 110.1px;
        }

        #qms { 
            width: 85.2px; 
            height: 99.4px;
        }

        #carbon-charter {
            width: 200.2px; 
            height: 80.1px;
        }


         //SOCIAL BUTTON WIDTH
        .social-button li a {
            width: 212.5px;
        }


        .news-image-parent {                  /////////////////////////////////animation
            display: inline-block;
            width: 455px;
            height: 261.844px;
            overflow: hidden;
        }

        .news-text {
            border-top: none;
            margin-top: -3px;
        }


        .news-1-image-child {           
            width: 100%;
            height: 100%;
            background-image: url("../../html-css-reflection/images/latest/news-1.jpeg");
            background-size: cover;
            background-position: center;
            border-radius: 3px 3px 0px 0px;
        }

        .news-2-image-child {           
            width: 100%;
            height: 100%;
            background-image: url("../../html-css-reflection/images/latest/news-2.jpeg");
            background-size: cover;
            background-position: center;
            border-radius: 3px 3px 0px 0px;
        }
        
        .news-3-image-child {           
            width: 100%;
            height: 100%;
            background-image: url("../../html-css-reflection/images/latest/news-3.jpeg");
            background-size: cover;
            background-position: center;
            border-radius: 3px 3px 0px 0px;
        }

        .news-image-parent > .news-1-image-child,
        .news-image-parent > .news-2-image-child,
        .news-image-parent> .news-3-image-child {
            transition: all .25s cubic-bezier(.215,.61,.355,1);
        }

        .news-image-parent:hover > .news-1-image-child,
        .news-image-parent:hover > .news-2-image-child,
        .news-image-parent:hover > .news-3-image-child {
            transform: scale(1.075);
        }

        .news-1 > a > img,
        .news-2 > a > img,
        .news-3 > a > img {
            display: none;
        }

        //NEWS TOOLTIPS 
        .news .tooltip .tooltip-text {
            width: 200px;
            margin-right: -100px;
        }

    }



